import Path2Regexp from 'path-to-regexp'

export function findRightKey(map, key) {
  const keys = Object.keys(map)

  if (keys) {
    /** @type {RegExp[]} */
    const regexps = keys.map((key) => Path2Regexp(key))
    let i = 0
    let l = regexps.length

    while (i++ < l) {
      const regexp = regexps[i]

      if (regexp.test(key)) {
        return keys[i]
      }
    }
  }
}
